<template>
  <div class="app-container">
    <!--创建卡券-->
    <createcoupon openmode="1"
                  :couponid="bindCardCouponId"
                  :couponType="type">
    </createcoupon>
  </div>
</template>

<script>
import createcoupon from '@/components/createCoupon/index' // 引入卡券组件

export default {
  name: 'createCoupon',
  components: { createcoupon }, // 创建卡券组件
  data () {
    return {
      bindCardCouponId: '',
      type: ''
    }
  },
  created () {
    this.bindCardCouponId = this.$route.query.id == undefined ? '' : this.$route.query.id // 接收ID
    console.log(this.$route.query.type)
    this.type = this.$route.query.type
  },

  mounted () {

  }
}
</script>

<style scoped>
</style>



